function StatusIcon({ status }: { status: string }) {
  switch (status) {
    case "best":
      return <span className="inline-flex items-center justify-center w-5 h-5 rounded-full bg-green-500/20"><CheckIcon className="w-3 h-3 text-green-400" /></span>;
    case "good":
      return <span className="inline-flex items-center justify-center w-5 h-5 rounded-full bg-blue-500/20"><CheckIcon className="w-3 h-3 text-blue-400" /></span>;
    case "neutral":
      return <span className="inline-flex items-center justify-center w-5 h-5 rounded-full bg-yellow-500/20"><Minus className="w-3 h-3 text-yellow-400" /></span>;
    case "bad":
      return <span className="inline-flex items-center justify-center w-5 h-5 rounded-full bg-red-500/20"><X className="w-3 h-3 text-red-400" /></span>;
    default:
      return null;
  }
}

